import { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useHttp } from '../../hooks/http.hook';
import './regForm.scss';

type PopupState = {
    isPopupHidden: boolean,
    setPopupState: (state: boolean) => void;
  };

const RegForm = ({
  isPopupHidden, setPopupState,
}: PopupState) => {
  const { loading, error, request } = useHttp();
  const history = useHistory();
  const [form, setForm] = useState({ login: '', password: '' });
  const changeHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [event.target.placeholder.toLowerCase()]: event.target.value });
  };

  const registerHandler = async () => {
    try {
      const data = await request('http://localhost:8080/api/auth/register', 'POST', { ...form });
    } catch (e) {
      throw new Error('Something went wrong');
    }
  };

  const loginHandler = async () => {
    try {
      const data = await request('http://localhost:8080/api/auth/login', 'POST', { ...form });
      if (data.token) {
        const { token, userId } = data;
        localStorage.setItem('userData', JSON.stringify({
          userId, token,
        }));
        setPopupState(true);
        history.push('/admin-page/categories');
      }
    } catch (e) {
      throw new Error('Something went wrong');
    }
  };
  return (
    <form className="login-form">
      <div className="form-group">
        <input type="login" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Login" onChange={changeHandler} />
      </div>
      <div className="form-group">
        <input type="password" className="form-control" id="exampleInputPassword1" placeholder="Password" onChange={changeHandler} />
      </div>
      <div className="reg-btns">
        <button type="button" className="btn-submit" onClick={() => { loginHandler(); }}>Login</button>
        <button type="button" className="btn-cancel" onClick={() => { setPopupState(true); }}>Cancel</button>
      </div>
    </form>
  );
};

export default RegForm;
