import {
  Link, NavLink, Route, Switch, useRouteMatch,
} from 'react-router-dom';
import CardsSection from '../cards-section/CardsSection';
import CategoriesSection from '../categories-section/CategoriesSection';
import './adminHeader.scss';

const AdminHeader = () => {
  const logout = () => {
    localStorage.removeItem('userData');
  };
  const { path, url } = useRouteMatch();
  return (
    <div className="adminHeader">
      <div className="headerContent">
        <ul className="nav-items">
          <li>
            <NavLink exact activeClassName="active" to={`${url}/categories`}>Categories</NavLink>
          </li>
          <li>
            <NavLink activeClassName="active" className="non-active" to={`${url}/categories/cards`}>Cards</NavLink>
          </li>
        </ul>

        <button className="logout-btn" type="button" onClick={() => { localStorage.removeItem('userData'); logout(); }}>
          <Link to="/">
            Logout
          </Link>
        </button>
      </div>

      <Switch>
        <Route exact path={path}>
          <CategoriesSection />
        </Route>
        <Route exact path={`${path}/categories`}>
          <CategoriesSection />
        </Route>
        <Route exact path={`${path}/categories/cards`}>
          <CardsSection />
        </Route>
      </Switch>
    </div>
  );
};

export default AdminHeader;
